import React, { Component } from 'react'
import { LargeHeading, SmallHeading } from '@/components/headings'
import { Switch } from '@/components/Switch'
import { ColoredButtons } from '@/components/button/index'
import './index.scss'

class FcAndDnd extends Component {
    state = {
        totalPush: false,
        generalPush: false,
        smartTrigger: false,
        geoTrigger: false,
        refreshFC: false,
        throttlePush: false,
        dnd: false,
    }
    render() {
        const {
            totalPush,
            generalPush,
            smartTrigger,
            geoTrigger,
            refreshFC,
            throttlePush,
            dnd,
        } = this.state
        console.log(this.state, 'state of fc')
        return (
            <div className="section">
                <LargeHeading>Common Setting</LargeHeading>
                <div className="mrgn-top-15 section">
                    <SmallHeading>Frequency Capping</SmallHeading>
                    <>
                        <Switch
                            onClick={() => this.setState({ refreshFC: !refreshFC })}
                            label="Refresh the frequency capping daily at 00:00 GMT"
                        />
                    </>
                    <>
                        <Switch
                            onClick={() => this.setState({ totalPush: !totalPush })}
                            label="Total push notificaitons send uptp"
                        />
                        {totalPush && (
                            <div className="margn-left-15">
                                <Switch
                                    className={`push-box ${generalPush ? 'active-box' : ''}`}
                                    label="General push notificaitons send uptp"
                                    onClick={() => this.setState({ generalPush: !generalPush })}
                                />
                                <Switch
                                    className={`push-box ${smartTrigger ? 'active-box' : ''}`}
                                    label="Smart Trigger push notificaitons send uptp"
                                    onClick={() => this.setState({ smartTrigger: !smartTrigger })}
                                />
                                <Switch
                                    className={`push-box ${geoTrigger ? 'active-box' : ''}`}
                                    label="Geo Fenced push notificaitons send uptp"
                                    onClick={() => this.setState({ geoTrigger: !geoTrigger })}
                                />
                            </div>
                        )}
                    </>
                </div>
                <div className="mrgn-top-15 row">
                    <div className="col mrgn-top-15">
                        <SmallHeading>General Push Setting</SmallHeading>
                        <div className="section">
                            <SmallHeading>Push Notification Throttling</SmallHeading>
                            <Switch
                                label="Throttle Pushes"
                                onClick={() => this.setState({ throttlePush: !throttlePush })}
                            />
                        </div>
                    </div>
                    <div className="col  mrgn-top-15">
                        <SmallHeading>Smart Trigger Campaigns</SmallHeading>
                        <div className="section">
                            <SmallHeading>Do Not Disturb</SmallHeading>
                            <Switch label="DND" onClick={() => this.setState({ dnd: !dnd })} />
                        </div>
                    </div>
                </div>

                <ColoredButtons className="small-btn">Save</ColoredButtons>
            </div>
        )
    }
}

export default FcAndDnd
