export const askForPermissioToReceiveNotifications = async () => {
    try {
        const messaging = firebase.messaging()

        await messaging.requestPermission()
        const token = await messaging.getToken()
        let data = {
            requestTimeStamp: 1544,
            source: 'WEB',
            token: token,
        }
        createPNUser(data)
        return token
    } catch (error) {
        console.error(error)
    }
}
