import Api from '@/utils/Api'

export function createPNUser(data) {
    Api.addPNUser(data)
        .then(res => {
            if (res) {
                console.log('Your Subscription is updated with user Id' + res.userId)
            }
        })
        .catch(err => {
            console.log('error is here', err)
        })
}

export function createPN(data) {
    Api.createPN(data)
        .then(res => {
            console.log('response is here', res)
        })
        .catch(err => {
            console.log('error is here', err)
        })
}
